.GameDiv {
    width: 400pt;
    height: 630pt;
    margin: auto;
    text-align: center;
}

h1,
h2,
h3,
h4,
h5,
p {
    color: white;
}

.GameBtn {
    cursor: pointer;
}

/* width */
::-webkit-scrollbar {
    width: 4px;
    height: 0px;
    margin: 0 !important;
}

/* Track */
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px transparent;
    border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #424242;
    border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #b30000;
}

@font-face {
    font-family: FONT ;
    src: url(../../assets/fonts/architep.ttf);
}

@font-face {
    font-family: Altus ;
    src: url(../../assets/fonts/iCielAltus.ttf);
}


@font-face {
    font-family: AltusSerif ;
    src: url(../../assets/fonts/iCielAltusSerif.ttf);
}

@font-face {
    font-family: Butcherandblock ;
    src: url(../../assets/fonts/Butcherandblock.ttf);
}


